.footer-wrapper {
  padding: 50px 0px;
}

.icon-round {
  background: linear-gradient(to right, #df5950, #451046 75%);
  height: 50px;
  width: 50px;
  border-radius: 50px;
  cursor: pointer;
}

.footer-wrapper a:not(:last-of-type) {
  margin-right: 10px;
}

.icon-round img {
  width: 45%;
}
